/**
 * A plugin to run all the axe rules and create generic feedback
 */

let $ = require("jquery");
let Plugin = require("../base");
let annotate = require("../shared/annotate")("everything");
let summary = require("../shared/summary");

let errorTemplate = require("./error.handlebars");
let summaryTemplate = require("./summary.handlebars");
require("./style.less");

class MultiPlugin extends Plugin {

    constructor(conf) {

        super();

        this.conf = conf;
        this.context;
        this.setContext(this.getConf().context);

        var context = this.getContext();

        var inclusions = []; // all rules to be included
        var exclusions = []; // all rules to be excluded
        var includedTags = []; // tags used to select rules to be included in scan
        var excludedTags = []; // tags used to remove rules to be included in scan
        var includedRules = []; // rules to be included in scan, except ones selected because in excludedTags
        var excludedRules = []; // rules to be included in scan, except ones to be removed because in excludedTags
        var desc = []; // used to build description string from options in conf

        var branding = this.conf.branding;

        this.options = this.conf.options;

        if (this.options) {

            if (this.options.rules) setRules(this.options.rules);

            this.runOnly = this.options.runOnly;

            if (this.runOnly) {
                if (this.runOnly.type === "tag") includedTags = this.runOnly.values;
                if (this.runOnly.type === "rule") includedRules = this.runOnly.values;
                if ((this.runOnly.type === "tags") && (this.runOnly.value)) {
                    includedTags = this.runOnly.value.include;
                    excludedTags = this.runOnly.value.exclude;
                }
            }
        }

        if (includedTags.length>0) {
            inclusions = this.rules(includedTags);
            desc.push("+ tags [" + includedTags +"]");
        }
        if (excludedTags.length>0) {
            exclusions = this.rules(excludedTags);
            desc.push("- tags [" + excludedTags +"]");
        }
        if (includedRules.length>0) {
            inclusions = inclusions.concat(includedRules);
            desc.push("+ rules [" + includedRules +"]");
        }
        if (excludedRules.length>0) {
            exclusions = exclusions.concat(excludedRules);
            desc.push("- rules [" + excludedRules +"]");
        }
        inclusions = dedup(inclusions);
        exclusions = dedup(exclusions);
        inclusions = this.filterRules(inclusions, exclusions);
        this.inclusions = inclusions;

        let inc = context.include || ['document'];
        let title = "+"+JSON.stringify(inc);
        title += (context.exclude.length > 0) ? ", -"+JSON.stringify(context.exclude) : "";

        if (branding) {
            this.title = branding.brand || "context: " + title;
            if (branding.application) this.description = branding.application;
        } else {
            this.title = "context: " + title;
            this.description = desc.join(' ');
        }

        /**
         * For each listed rule, add to includedRules or excludedRules arrays, based on whether enabled property is true
         * @param {Object} rules - Rules to include or exclude from a scan
         */
        function setRules(rules) {
            if (rules) {
                Object.keys(rules).forEach((rule) => {
                    if (rules[rule].enabled === "true") {
                        includedRules.push(rule);
                    } else {
                        excludedRules.push(rule);
                    }
                });
            }
        }

        /**
         * Removes duplicates from an Array by returning it as a Set
         * @param ary - An array that may contain duplicate elements
         * @returns {Set} A set with no duplicated elements
         */
        function dedup(ary) {
            return new Set(ary);
        }
    }



    /**
     * Determines whether a rule should run, assuming a document-level context
     * @param  {Rule}    rule     The rule to test
     * @param  {Object}  options  Options object
     * @return {Boolean}
     */
    /*ruleShouldRun = function (rule, options) {
        'use strict';
        var runOnly = options.runOnly || {};
        var ruleOptions = (options.rules || {})[rule.id];

        // First, runOnly type rule overrides anything else
        if (runOnly.type === 'rule') {
            return runOnly.values.indexOf(rule.id) !== -1;

            // Second, if options.rules[rule].enabled is set, it overrides all
        } else if (ruleOptions && typeof ruleOptions.enabled === 'boolean') {
            return ruleOptions.enabled;

            // Third, if tags are set, look at those
        } else if(runOnly.type === 'tag' && runOnly.values) {
            return matchTags(rule, runOnly.values);

            // If nothing is set, only check for default excludes
        } else {
            return matchTags(rule, []);
        }

    };*/

    /**
     * Take Set of rule ids to include and Set to exclude and return Array of filtered rule ids
     * Important that it takes Sets and return an Array
     * See <a href="http://www.2ality.com/2015/01/es6-maps-sets.html">ES6 Sets</a> and
     * <a href="https://github.com/dequelabs/axe-core/blob/master/doc/rule-descriptions.md">axe-core rules</a>
     * @summary Remove rules in second set from rules in first
     * @param {Set} inc - List of rule ids to include in scan
     * @param {Set} ex - List of rule ids to exclude from scan
     * @returns {Array} Filtered list of rule ids
     */
    filterRules(inc, ex) {
        console.log("Included: ");
        console.log(inc);
        console.log("Excluded: ");
        console.log(ex);
        var inc = Array.from(inc);
        var difference = Array.from(new Set(inc.filter(r => !ex.has(r))));
        return difference;
    }

    /**
     * Return all rules associated with certain tags
     * See <a href="https://github.com/dequelabs/axe-core/blob/master/doc/rule-descriptions.md">axe-core rules</a>
     * @param {Array} tags - Array of tags used to filter returned rules
     * @returns {Array} Array of rule ids
     */
    rules(tags) {
        return Array.from(axe.getRules(tags), r => r.ruleId)
    }

    /**
     * Returns the Array of rule ids to include in a scan as a Set
     * @returns {Set}
     */
    getInclusions() {
        return new Set(this.inclusions);
    }

    //UNUSED?
    setTitle(title) {
        this.title = title;
        this.panel.setTitle(this.title);
    }



    /**
     * Getter for conf object
     * @returns {*}
     */
    getConf() {
        return this.conf;
    }

    setContext(context) {
        this.context = context;
    }
    getContext() {
        return this.context;
    }

    getOptions() {
        return this.getConf().options;
    }

    getTitle() {
        return this.title;
    }

    getDescription() {
        return this.description;
    }

    run() {

        let that = this;
        this.panel.errors = [];
        //console.log(that.getContext());
        console.log();
        axe.a11yCheck(this.getContext(), this.getOptions(), function (results) {
            if (results.violations.length) {
                $(results.violations).each((j, rule) => {
                    let impact = rule.impact;
                    let help = rule.help;
                    let bestpractice = (rule.tags.indexOf('best-practice') !== -1);
                    $(rule.nodes).each((i, node) => {
                        let $el = $(node.target[0]);

                        let entry = that.error(
                            help, summary(node) + errorTemplate({impact:impact, bestpractice:bestpractice}), $el);

                        annotate.errorLabel(
                            $el, "", help, entry);
                    });
                });
            }


            var ruleSet = Array.from(new Set(axe.getRules().filter(r => that.getInclusions().has(r.ruleId))));
            //console.log(ruleSet);
            that.panel.setTitle(that.getTitle());
            that.summary(summaryTemplate({violations: results.violations, passes: results.passes}));
            that.panel.render();

        });
    }

    cleanup() {
        annotate.removeAll();
    }
}

module.exports = MultiPlugin;



